import { useState, useEffect, ChangeEvent, FormEvent } from "react";

// Define types for form data and user
interface User {
  id?: number;
  firstName: string;
  lastName: string;
  address?: string;
  phone?: string;
  email: string;
}

export default function Home() {
  const [formData, setFormData] = useState<User>({
    firstName: "",
    lastName: "",
    address: "",
    phone: "",
    email: "",
  });

  const [users, setUsers] = useState<User[]>([]);

  // Fetch users when the component mounts
  useEffect(() => {
    fetchUsers();
  }, []);

  const fetchUsers = async () => {
    try {
      const response = await fetch("http://localhost:5000/users"); // Replace with your backend URL
      if (!response.ok) {
        throw new Error("Failed to fetch users");
      }
      const data: User[] = await response.json();
      setUsers(data);
    } catch (error) {
      console.error("Error fetching users:", error);
    }
  };

  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    try {
      const response = await fetch("http://localhost:5000/user", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formData),
      });

      if (response.ok) {
        fetchUsers(); // Refresh user list after successful submission
        setFormData({ firstName: "", lastName: "", address: "", phone: "", email: "" });
      } else {
        console.error("Failed to add user");
      }
    } catch (error) {
      console.error("Error submitting form:", error);
    }
  };

  const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData((prevFormData) => ({ ...prevFormData, [name]: value }));
  };

  return (
    <div>
      <h1>User Form</h1>
      <form onSubmit={handleSubmit}>
        <input
          name="firstName"
          placeholder="First Name"
          value={formData.firstName}
          onChange={handleChange}
        />
        <input
          name="lastName"
          placeholder="Last Name"
          value={formData.lastName}
          onChange={handleChange}
        />
        <input
          name="address"
          placeholder="Address"
          value={formData.address || ""}
          onChange={handleChange}
        />
        <input
          name="phone"
          placeholder="Phone"
          value={formData.phone || ""}
          onChange={handleChange}
        />
        <input
          name="email"
          placeholder="Email"
          value={formData.email}
          onChange={handleChange}
        />
        <button type="submit">Submit</button>
      </form>

      <h2>Submitted Users</h2>
      <ul>
        {users.map((user, index) => (
          <li key={index}>
            {user.firstName} {user.lastName}, {user.address}, {user.phone}, {user.email}
          </li>
        ))}
      </ul>
    </div>
  );
}
